.sesion{
    width: 100vw;
    min-height: 100vh;
    padding-top: 80px;
    background: url("https://s0.smartresize.com/wallpaper/737/463/HD-wallpaper-abstract-pink-patterned-background-vector-premium-nunny-vector-background-pattern-abstract-background-background-patterns-peach-abstract.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.row2{
    margin-top: 100px;
    margin-left: 100px;
    margin-right: 800px;
    max-width: 900px;
}

.inic-tit{
    margin-top: 200px;
    color: rgb(210, 106, 68);
    font-family: Cochin;
}

.bot-inic{
    width: 200px;
    color: brown;
    font-size: 15px;
    font-weight: bold;
    border: 2px solid rgb(105, 43, 43);
    border-radius: 20px;
    background-color: rgb(206, 135, 110);
    box-shadow: 5px 5px 7px rgb(156, 54, 16);
}

.bot-inic:hover {
    background-color: rgb(184, 100, 70);
    color: white;                        
    box-shadow: 3px 3px 10px rgb(90, 30, 10); 
}

.input-error {
    border: 1px solid red !important;
}

.error-text {
    color: red;
    font-size: 0.85rem;
    margin-top: -10px;
    margin-bottom: 10px;
}